<template>
    <div class="h5-my-like">
        <van-nav-bar
            title="我的收藏"
            left-text="返回"
            left-arrow
            fixed
            @click-left="$router.go(-1)"
        />
        <van-sticky :offset-top="46">
            <van-notice-bar mode="closeable">
                请向左滑动进行操作! 请向左滑动进行操作! 请向左滑动进行操作!
            </van-notice-bar>
        </van-sticky>
        <transition-group tag="ul" name="fade">
            <li v-for="item in $store.state.blog.myLikedBlogs" :key="item._id">
                <van-swipe-cell>
                    <MyPageBlogItem :blog="item" />
                    <template #right>
                        <van-button
                            square
                            text="删除"
                            type="danger"
                            class="delete-button"
                            @click.stop="handleLiked(item._id)"
                        />
                    </template>
                </van-swipe-cell>
            </li>
        </transition-group>
        <EmtyTips v-show="!$store.state.blog.myLikedBlogs.length" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            del: true
        };
    },
    mounted() {
        this.$store.dispatch("getMyliked");
    },
    methods: {
        async handleLiked(id) {
            let likes = await this.$store.dispatch("toggleLike", {
                blogID: id,
                userID: this.user._id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.h5-my-like {
    width: 100%;
    font-size: 16px;
    box-sizing: border-box;
    padding-top: 46px;
}
.delete-button {
    height: 100%;
}
</style>